@import '../base.scss';

.c-fiq {
  display: flex;
  flex-direction: column;
  width: 100%;
  position: relative;
  max-width: 100%!important;
  @include t-gutters(true);
  @include t-v-gutters(true);
  @include t-v-gutters;
  margin-top: 0!important;

  &__bg-image-wrap {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
  }
  &__bg-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
  }

  &__content {
    display: flex;
    z-index: 2;
    flex-direction: column;
    align-items: center;

    &--above-title {
      margin-bottom: t-to-rem(28);
      display: flex;
      align-items: center;
      @include t-media-query($s-medium-down) {
        margin-bottom: t-to-rem(52);
      }
      p {
        font-weight: 700;
        font-size: t-to-rem(24);
        line-height: t-to-rem(29);
        letter-spacing: 0.2em;
        color: #FFFFFF;
        text-transform: uppercase;
        margin: 0;
      }
    }

    &--company-images {
      display: flex;
      flex-direction: row;
      align-items: center;
      column-gap: t-to-rem(72);
      margin-bottom: t-to-rem(72);
      max-width: 100%;
      @include t-media-query($s-medium-down) {
        column-gap: t-to-rem(52);
        margin-bottom: t-to-rem(52);
        overflow-x: auto;
      }
    }

    &--company-image-wrap {
      display: flex;
      height: t-to-rem(90);
      padding: t-to-rem(15);
      cursor: pointer;
      align-items: center;
      &.active {
        border: 1px solid #FFFFFF;
      }
      img {
        max-height: t-to-rem(60);
      }
      @include t-media-query($s-medium-down) {
        flex-shrink: 0;
        height: t-to-rem(40);
        padding: t-to-rem(5);
        img {
          height: 100%;
          max-height: t-to-rem(30);
          width: auto;
        }
      }
    }
    &--quotes, &--quote-authors {
      width: 100%;
      display: flex;
      flex-direction: column;
    }

    &--quote {
      margin-bottom: t-to-rem(50);
      display: none;
      align-items: center;
      justify-content: center;
      &.active {
        display: flex;
      }
      p {
        font-weight: 500;
        font-size: t-to-rem(36);
        line-height: t-to-rem(43);
        text-align: center;
        color: #FFFFFF;
        margin: 0;
      }
      @include t-media-query($s-medium-down) {
        margin-bottom: t-to-rem(24);
        p {
          font-size: t-to-rem(20);
          line-height: t-to-rem(24);
        }
      }
    }
    &--quote-author {
      display: none;
      align-items: center;
      justify-content: center;
      &.active {
        display: flex;
      }
      p {
        font-weight: 500;
        font-size: t-to-rem(24);
        line-height: t-to-rem(29);
        text-align: center;
        color: #FFFFFF;
        margin: 0;
      }
      @include t-media-query($s-medium-down) {
        p {
          font-size: t-to-rem(16);
          line-height: t-to-rem(19);
        }
      }
    }
  }
}